@import url(https://fonts.googleapis.com/css2?family=Averia+Gruesa+Libre&family=Courgette&family=Rubik:wght@300&family=Secular+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Abel&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Fjalla+One&display=swap);
.navContainer{position:fixed;display:flex;z-index:1;justify-items:stretch;width:100%;padding:0px;margin:0px;height:10%;background-color:#621055}.nav{display:flex;width:100%;height:100%;font-size:1.25em;text-transform:uppercase;flex-direction:row;align-items:center;justify-content:space-around}.nav a{color:white;font-family:"Abel", sans-serif}.nav a:link{text-decoration:none}.nav a:link:hover{opacity:0.7}.nav button{background-color:inherit;position:relative;right:40px;color:black;border:none}.homePage{position:relative;top:10vh;background-color:#ed6663;display:grid;grid-gap:2em;height:100vh;grid-template-columns:repeat(12, 1fr);grid-template-rows:repeat(12, 1fr)}.homePage .intro{grid-column:2 / span 6;text-align:center;grid-row:3 / span 8;background-color:#b52b65;border-radius:1em;font-size:large;padding:2em}.homePage .intro a{color:white}.homePage .intro a:hover{opacity:0.7;display:inline-block;transform:scale(1.4)}.homePage .intro .homeTittle{font-weight:bold;text-transform:uppercase;font-family:"Averia Gruesa Libre", cursive}.homePage .homeSignin{grid-column:8 / span 4;grid-row:3 / span 8;text-align:center;display:flex;background-color:#ffa372;border-radius:1em;justify-content:center;color:black;align-items:center;font-family:"Fjalla One", sans-serif}.homePage .homeSignin input{background:#ed6663}.homePage .homeSignin button{border-radius:10%;background-color:#ed6663;color:white;box-shadow:0px 0px 10px white}.shop{position:relative;top:10vh;height:100vh;display:grid;background-color:#ed6663;grid-gap:0em;grid-template-columns:repeat(12, 1fr);grid-template-rows:repeat(12, 1fr)}.shop .shopMain{grid-column:3 / span 8;grid-row:1 / span 12;display:flex;padding:0.4em;flex-direction:row;flex-wrap:wrap;overflow:scroll;background-color:#b52b65}.shop .shopNav{grid-column:1 / span 2;grid-row:1 / span 12;background-color:#e6d5a9;background-size:cover;margin-right:0.5em}.shop .shopNav .shopTypeProduct{width:80%;font-family:"Abel", sans-serif;margin:1em}.shop .shopNav .shopTypeProduct input{width:100%;margin-top:0.5em}.shop .shopNav .shopSideBar{display:flex;flex-direction:column;margin:0em}.shop .shopNav .shopSideBar a{padding:0.5em;text-decoration:none;text-transform:uppercase;color:black;font-family:"Fjalla One", sans-serif}.shop .shopNav .shopSideBar a:hover{width:100%;color:white;background-color:#621055}.shop .shopMain::-webkit-scrollbar-track{background-color:orange}.shop .shopMain::-webkit-scrollbar-thumb{box-shadow:inset 0 0 10px #621055;border-radius:1em}.shop .shopMain::-webkit-scrollbar{width:10px}.shop .shopAdvertisement{grid-column:11 / span 2;grid-row:1 / span 12;background:url(/shoppingcart/static/media/advertise.8bc1b03d.jpg);background-size:cover}.item{height:20em;width:15em;border-radius:0.5em;margin:0.5em;padding:0.5em;border:2px solid #621055;background-color:white;display:grid;grid-template-areas:"image image image" "image image image" "name name price" "buy remain heart"}.item .itemImg{grid-area:image;display:inline;width:220px;height:200px}.item .itemName{grid-area:name;font-size:1.3em}.item .itemPrice{display:flex;align-items:center;justify-content:center;grid-area:price;font-size:0.75em}.item .itemBuy{grid-area:buy;background-color:#ffa372;font-size:0.7em;padding:-0.5em}.item .itemHeart{grid-area:heart;color:#621055;margin:auto;background-color:transparent;display:inline-block;border:none}.item .itemHeart:hover{transform:scale(1.2)}.item .itemRemain{grid-area:remain}.EmptyCart{position:relative;top:10em}.EmptyCart .trash{height:100%;background-color:red;display:flex;align-items:center;justify-content:center}.cart{position:relative;top:10vh;width:100vw;height:100vh;display:flex;flex-direction:column;background-color:#ed6663;align-items:center}.itemInCart{margin-top:1em;display:flex;flex-direction:row;align-items:center;justify-content:space-between;width:1000px;height:100px}.itemInCart .imgInCart{width:100px;height:100px}.itemInCart .quantity{display:flex;flex-direction:row}.footer{position:relative;top:10vh}.cartPurchase{display:flex;margin-top:2em;width:80%;flex-direction:row;justify-content:space-between;background-color:red}

